import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Subscription } from 'rxjs';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed, IPageCommon } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from '../../psServices/omni-common/omni-pull.service';
import { IOptionsPsAccountsListExposed } from '../ps-accounts-list/ps-accounts-list.component.interfaces';
import { IAccountStatementDetails } from './../../../pages/account-statement-list/account-statement-list.page.iterface';

@Component({
  selector: 'ps-account-statement-list',
  templateUrl: './ps-account-statement-list.component.html',
  styleUrls: ['./ps-account-statement-list.component.scss'],
})
export class PsAccountStatementListComponent extends PsTemplateListPage implements OnInit {
  @Output() reloadFct = new EventEmitter<string>();
  @Input() options: IOptionsPsAccountsListExposed = {};
  eventSub: Subscription;
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  constructor(
    private omniPull?: OmniPullService) {
    super();
    this.populateHeaderValueMap();
  }

  ngOnInit() {
    super.init();
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
  }

  prepareLookUp(card: IAccountStatementDetails) {
    const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.itemList = this.options.itemList;
    if (card.merchantName && card.merchantLogo) {
      lookupDetails.logoOptions = {
        imageOptions: {
          imageName: card.merchantLogo.toLowerCase() + '.png',
          imageBase64Url: '',
          imagePath: PsApplicationSettings.CLIENT_ASSETS_CONFIG.IMGS_FOLDER_URL
        },
        label: card.merchantName
      };
      lookupDetails.actionDetailsOptions = [{
        id: 'merchant-details',
        label: 'merchant_details_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'merchant-details',
            labelOptions: {
              labelKey: 'merchant_details_key'
            }
          },
          route: 'merchant-details',
          pageOptions: {
            operId: CommonBussinessConstant.MERCHANT_DETAILS,
            iconName: 'merchant-details',
            title: 'merchant_details_key',
          },
          navigationOptions: {
            queryParams: { card: card },
          },
        }
      }];


    }

    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }

    return lookupDetails;
  }

  reloadList() {
    this.reloadFct.emit();
  }

  private populateHeaderValueMap() {
    let transactionTypeMap: IMapKeyValue = {};
    let transactionDateMap: IMapKeyValue = {};
    let transactionReferenceMap: IMapKeyValue = {};
    let amountMap: IMapKeyValue = {};
    let valueDateMap: IMapKeyValue = {};
    let branchMap: IMapKeyValue = {};
    let descriptionMap: IMapKeyValue = {};
    let fromAccountMap: IMapKeyValue = {};
    let toAccountMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};
    let chequeNoMap: IMapKeyValue = {};
    let chequeDateMap: IMapKeyValue = {};
    let payeeNameMap: IMapKeyValue = {};


    transactionReferenceMap = {
      key: 'reference',
      value: '--- --- --- ---',
      isEdit: false,
    };

    amountMap = {
      key: 'transactionAmount',
      value: '--- --- --- ---',
      isEdit: false,
    };
    // valueDateMap = {
    //   key: 'startDate',
    //   value: '--- --- --- ---',
    //   isEdit: false,
    //   showMappingOnNoValue: false,
    // };
    // branchMap = {
    //   key: 'branch',
    //   value: '----',
    //   isEdit: false,
    //   showMappingOnNoValue: false,
    // };
    descriptionMap = {
      key: 'description',
      value: '--- --- --- ---',
      isEdit: false,
      showMappingOnNoValue: false,
    };
    fromAccountMap = {
      key: 'fromAccount',
      value: '--- --- --- ---',
      isEdit: false,
    };
    toAccountMap = {
      key: 'toAccount',
      value: '--- --- --- ---',
      isEdit: false,
    };

    chequeNoMap = {
      key: 'chequeNo',
      value: '--- --- --- ---',
      isEdit: false,
    };

    chequeDateMap = {
      key: 'chequeDate',
      value: '--- --- --- ---',
      isEdit: false,
    };

    payeeNameMap = {
      key: 'payeeName',
      value: '--- --- --- ---',
      isEdit: false,
    };


    headerTitleMap = {
      key: 'title',
      value: 'transactionName',
      isEdit: false,
    }
    headerSubtitleMap = {
      key: 'subTitle',
      value: 'transactionDate',
      isEdit: false,
      formGroupMap: {
        placeHolder: 'transaction_date',
        fcName: 'transactionDate',
      }
    }
    this.labelValuesMap.set('value_date_key', valueDateMap);
    this.labelValuesMap.set('description_key', descriptionMap);
    this.labelValuesMap.set('branch_key', branchMap);
    this.labelValuesMap.set('reference_no_key', transactionReferenceMap);
    this.labelValuesMap.set('amount_key', amountMap);
    this.labelValuesMap.set('from_account_number_key', fromAccountMap);
    this.labelValuesMap.set('to_account_number_key', toAccountMap);
    this.labelValuesMap.set('cheque_date_key', chequeDateMap);
    this.labelValuesMap.set('cheque_no_key', chequeNoMap);
    this.labelValuesMap.set('payee_name_date_key', payeeNameMap);
    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subtitle', headerSubtitleMap);



  }
  //handles the click event on merchant logo
  onLogoClicked(card) {
    this.gotoMerchantDetails(card);
  }
  gotoMerchantDetails(card) {
    const page: IPageCommon = {
      operID: ConstantCommon.VIEW_STATEMENT_OF_TRANSACTIONS,
      title: 'Merchant Details',
      icon: 'report',
      component: 'merchant-details',
      param: card
    };
    this.navService.openPage(page);
  }
}

